@import './var.scss';
@import './default.scss';


// top start

.top{
    font-size: 18px;
    color: $primary-color;
    padding: 18px 0;

    .icon{
        font-size: 20px;
    }
 
    .center{
        display: flex;
        justify-content: space-between;

        
    }

    &-left{
        display: flex;
        justify-content: flex-start;

        & > div{
            display: flex;
            justify-content: flex-start;
    
            span{
             

                &.icon{
                    margin-right: 5px;
                }
            }

            
        }
        &__email{
            margin-left: 24px;

            &--actual-email{
                text-decoration: underline;
                color:$primary-color;
            }
         }
    }


    &-right{
        display: flex;
        justify-content: flex-start;

        a{
            color: $primary-color;
            font-size: 26px;
            margin-left: 16px;

        }

    }
}


// top ends

// nav starts

nav{
    -webkit-box-shadow: 0px 6px 4px -2px rgba(132,132,132,1);
    -moz-box-shadow: 0px 6px 4px -2px rgba(132,132,132,1);
    box-shadow: 0px 6px 4px -2px rgba(132,132,132,1);
    
    .center{    
        display: flex;
        justify-content: space-between;
        position: relative;
    }

    .logo{
        padding: 10px 0;
        img{
            display: block;
        }
    }

    ul{
        display: flex;
        justify-content: flex-start;

        li{
            list-style: none;
            margin-left: 18px;

            a{
                color: $secondary-color;
                display: block;
                height: 60px;
                line-height: 60px;
                font-size: 18px;

                &:hover{
                    color: $primary-color;
                }
            }

            .active{
                color: $primary-color;
            }
        }
    }

    .down-lists{
        display: none;
    }
}

// nav ends

